<template>
  <!-- Start Contact -->
  <div class="container py-5">
    <div class="row py-5">
      <div class="col-md-9 m-auto" method="post" role="form">
        <div class="row">
          <div class="form-group col-md-6 mb-3">
            <label>Email</label>
            <input v-model="email" type="email" class="form-control mt-1" id="email" name="email"
                   placeholder="Email">
          </div>
          <div class="form-group col-md-6 mb-3">
            <label>Password</label>
            <input v-model="password" type="text" class="form-control mt-1" id="password" name="password"
                   placeholder="Password">
          </div>
          <div class="form-group col-md-6 mb-3">
            <label>Username</label>
            <input v-model="username" type="text" class="form-control mt-1" id="username" name="username"
                   placeholder="Username">
          </div>
          <div class="form-group col-md-6 mb-3">
            <label>Status</label>
            <select v-model="status" class="form-control mt-1">
              <option>Pending</option>
              <option>Register</option>
            </select>

          </div>

          <div class="form-group mb-3">
            <label>Role</label>
            <select v-model="role" class="form-control mt-1">
              <option>Admin</option>
              <option>client</option>
            </select>
          </div>
        </div>


        <div class="row">
          <div class="col text-end mt-2">
            <button class="btn btn-success btn-lg px-3" @click="this.create">Create</button>
          </div>
        </div>


      </div>
    </div>
  </div>
  <!-- End Contact -->
</template>

<script>
import {Comunication} from "@/red/comunicationMethods";
import endpoints from "@/red/endpoints";

const comunication = new Comunication(endpoints.base_url)
export default {
  name: "create",
  data() {
    return {
      role: '',
      status: '',
      email: '',
      password: '',
      username: '',
      errors: []
    }
  }, methods: {
    async create() {
      if (this.email == '' || this.password == '' || this.username == '' || this.status == '' || this.role == '') {
        this.errors.push({
          message: 'register error',
          code: 400
        })
      }

      const body = {
        email: this.email,
        password: this.password,
        username: this.username,
        status: this.status,
        roles: [this.role]
      }
      console.log(body)
      comunication.setToken(this.$store.state.token)
      let result = await comunication.post('user/create', body)

      if (result.status != 200) {
        const error = result.data
        this.errors.push(error)
        console.log(this.errors)
        return
      }
      result = result.data
      console.log(result)
      await this.$router.back()
    }
  }
}
</script>

